import React from 'react';
import { connect } from 'react-redux';
import { toggleProductionContainer } from '../../redux/production/production.actions';
import { motion } from 'framer-motion';

import Video from '../video/video.component';

import './card-container.styles.scss';

const CardContainer = ({toggleProductionContainer}) => (
    <motion.div 
        initial={{ opacity: 0, scale: 0.5 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{duration: 1}}
        className='card-container' 
        onClick={toggleProductionContainer}
    >
        <h1 className='title'>Hanzlik.sk</h1>
        <div className='production'>
            <div className='desc'>
                <p>
                    Hlavná a zároveň všeobecne zameraná produkcia na tvorenie videoklipov, reklamných videi, aftermovie's a podobne zameraných videí.
                </p>
            </div>
            <button className='btn'>Preisť na web</button>
            <h3 className='title'>Ukážka</h3>
            <div className='sample'>
                <Video video={'https://www.youtube.com/watch?v=l4qVw8OxjHc'} play/>
            </div>
        </div>
    </motion.div>
)

const mapDispatchToProps = dispatch => ({
    toggleProductionContainer: () => dispatch(toggleProductionContainer())
})

export default connect(null, mapDispatchToProps)(CardContainer);